import * as yup from "yup";
import { AddHrRequest } from "../../types";

export const ValidationSchema = yup.object().shape({
  maxReservedStudents: yup
    .number()
    .min(0, "Liczba musi by dodatnia!")
    .max(30, "Maksymalna liczba rezerwacji to 30!")
    .integer("Liczba musi by cakowita!")
    .required("Wprowad liczb!")
    .typeError("Wprowad liczb!"),
  fullName: yup.string().required("Wprowad imi i nazwisko!"),
  email: yup
    .string()
    .email("Niepoprawny adres email!")
    .required("Wprowad adres email!"),
  company: yup.string().required("Wprowad nazw firmy!"),
});

export const initialHrInfoValues: AddHrRequest = {
  fullName: "",
  email: "",
  company: "",
  maxReservedStudents: 10,
};
